import React from 'react';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Controller } from 'swiper';
import 'swiper/css';
import MainSwiperLi from '../../02_Molecules/MainSwiperLi/index';
import { PC, Mobile } from 'components/04_Templates/MediaQuery/MediaQuery';
import { SrOnly } from 'styles/Globals';

const MainSwiper = () => {
	// const [controlledSwiper, setControlledSwiper] = useState(null);
	return (
		<>
			<PC>
				<section css={pcSectionStyle}>
					<h2></h2>
					<Swiper
						{...swiperOption}
						modules={[Controller]}
						// controller={{ control: controlledSwiper }}
					>
						{data &&
							data.map((item) => (
								<SwiperSlide key={item.id}>
									<MainSwiperLi {...item} />
								</SwiperSlide>
							))}
					</Swiper>
				</section>
			</PC>
			<Mobile>
				<section css={moSectionStyle}>
					<h2></h2>
					<Swiper
						{...swiperOption}
						modules={[Controller]}
						// controller={{ control: controlledSwiper }}
					>
						{data &&
							data.map((item) => (
								<SwiperSlide key={item.id}>
									<MainSwiperLi {...item} />
								</SwiperSlide>
							))}
					</Swiper>
				</section>
			</Mobile>
		</>
	);
};

const swiperOption = {
	spaceBetween: 0,
	slidesPerView: 1,
	// onSlideChange: () => console.log('slide change'),
	// onSwiper: (swiper) => console.log(swiper),
};

const data = [
	{
		id: 0,
		title: '  ! ',
		sub: '     ,    ',
		pic: 'sample_1050_1',
		bg: '#FE5000',
		color: '#fff',
		lnk: '/',
	},
	{
		id: 1,
		title: '    ',
		sub: '   .',
		pic: 'sample_1050_2',
		bg: 'blue',
		color: '#fff',
		lnk: '/',
	},
	{
		id: 2,
		title: ' ,  ',
		sub: '     ',
		pic: 'sample_1050_3',
		bg: 'green',
		color: '#fff',
		lnk: '/',
	},
];

const pcSectionStyle = css`
	height: 45rem;
	& > h2 {
		${SrOnly};
	}
	.swiper {
		overflow: visible;
	}
`;
const moSectionStyle = css`
	margin-bottom: 2rem;
	& > h2 {
		${SrOnly};
	}
	.swiper {
		/* overflow: visible; */
	}
`;

export default MainSwiper;
