import React from 'react';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import PropTypes from 'prop-types';
import Unit from 'components/02_Molecules/PUnit/PUnit';
import { PC, Mobile } from 'components/04_Templates/MediaQuery/MediaQuery';
import theme from 'styles/theme';

const PList = ({ divide = '4' }) => {
	const pcSwiperOption = {
		spaceBetween: 20,
		slidesPerView: divide,
		// onSlideChange: () => console.log('slide change'),
		// onSwiper: (swiper) => console.log(swiper),
	};
	const moSwiperOption = {
		spaceBetween: 20,
		slidesPerView: 2,
	};
	const { ttl, list } = data;
	return (
		<>
			<PC>
				<section
					css={pcPlistStyle}
					// className={[divide ? `list${divide}` : null].join(' ')}
				>
					<h2>{ttl}</h2>
					<Swiper {...pcSwiperOption}>
						{list &&
							list.map((item) => (
								<SwiperSlide key={item.id}>
									<Unit {...item} />
								</SwiperSlide>
							))}
					</Swiper>
				</section>
			</PC>
			<Mobile>
				<section
					css={moPlistStyle}
					// className={[divide ? `list${divide}` : null].join(' ')}
				>
					<h2>{ttl}</h2>
					<Swiper {...moSwiperOption}>
						{list &&
							list.map((item) => (
								<SwiperSlide key={item.id}>
									<Unit {...item} />
								</SwiperSlide>
							))}
					</Swiper>
				</section>
			</Mobile>
		</>
	);
};

PList.propTypes = {
	ttl: PropTypes.string,
	divide: PropTypes.string,
};

const data = {
	ttl: '',
	list: [
		{
			id: 0,
			name: '',
			title: '[BEST]   ,    ! (, ,   OK)',
			pic: 'sample_750_0',
			price: '35800',
			term: '5',
			lnk: '/',
		},
		{
			id: 1,
			name: '',
			title: '[New]    ,  ?',
			pic: 'sample_750_1',
			price: '2600',
			term: '5',
			lnk: '/',
		},
		{
			id: 2,
			name: '',
			title: '   ',
			pic: 'sample_750_2',
			price: '17800',
			term: '5',
			lnk: '/',
		},
		{
			id: 3,
			name: '',
			title: '[]   ',
			pic: 'sample_750_3',
			price: '2000',
			term: '2',
			lnk: '/',
		},
		{
			id: 4,
			name: '',
			title: '[BEST]  ,   ',
			pic: 'sample_750_4',
			price: '165800',
			term: '5',
			lnk: '/',
		},
		{
			id: 5,
			name: '',
			title: '     ',
			pic: 'sample_750_5',
			price: '39500',
			term: '5',
			lnk: '/',
		},
	],
};

const pcPlistStyle = css`
	width: 120rem;
	margin: 0 auto;
	h2 {
		margin: 0;
		padding: 2rem 0 0 0;
		color: ${theme.color.def};
	}
`;
const moPlistStyle = css`
	width: 100%;
	margin: 0 auto;
	padding: 0 1rem;
	h2 {
		margin: 0;
		padding: 2rem 0 0 0;
		font-size: ${theme.size.lg};
		color: ${theme.color.def};
	}
`;

export default PList;
